import React from 'react';
import Logo from '../img/logo.png'
import './index.css'
import { useNavigate } from 'react-router';


const Navbar = () => {
    const navigate = useNavigate()
    const { loginStatus, userName } = sessionStorage
    console.log(loginStatus)
    const logoutUser = () => {
        // remove the logged users details from session storage
        sessionStorage.removeItem('loginStatus')
        sessionStorage.removeItem('userId')
        sessionStorage.removeItem('userName')
        sessionStorage.removeItem('Token')

        navigate('/')
    }

    return (
        <div >
            <br />
            <nav class="navbar navbar-expand-lg navbar-light bg-light">
                <div class="container-fluid" id="nav">
                    <img src={Logo} width="100" height="120" alt="Logo" onClick={() => navigate("/")}></img>
                    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span class="navbar-toggler-icon"></span>
                    </button>
                    <div class="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul class="navbar-nav me-auto mb-2 mb-lg-0">

                            <li class="nav-item">
                                <a class="nav-link active" aria-current="page" onClick={() => navigate("/")}>Home</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" onClick={() => navigate("/MyPolicies")}>My Policies</a>
                            </li>
                            <li class="nav-item">
                                <a class="nav-link" onClick={() => navigate("/PremiumCal")}>Premium Calculator</a>
                            </li>
                            {loginStatus != 1 ?
                                <li class="nav-item dropdown" >
                                    <a class="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Signin</a>
                                    <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
                                        <li><a class="dropdown-item" onClick={() => navigate("/userLogin")}>User</a></li>
                                        <li><a class="dropdown-item" onClick={() => navigate("/officerLogin")}>Officer</a></li>
                                    </ul>
                                </li> :
                                <li class="nav-item dropdown">
                                    <a class="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Welcome {userName}</a>
                                    <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
                                        <li><button class="dropdown-item" onClick={logoutUser}>Logout</button></li>
                                    </ul>
                                </li>
                            }
                        </ul>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Navbar